import { calOptions } from './calOptions'
import calSeriesMap from './calSeriesMap'
import calSeriesColor from './calSeriesColor'
import calLegendData from './calLegendData'
import calAxisData from './calAxisData'
import calAxisRadarData from './calAxisRadarData'

import calChartBarData from './calChartBarData'
import calChartLineData from './calChartLineData'
import calChartPieData from './calChartPieData'
import calChartRadarData from './calChartRadarData'
import calChartScatterData from './calChartScatterData'
import calChartFunnelData from './calChartFunnelData'
import calChartCandlestickData from './calChartCandlestickData'
import calChartHeatmapData from './calChartHeatmapData'
import calChartTreemapData from './calChartTreemapData'
import calChartTagCloudData from './calChartTagCloudData'

export default function calChartsData() {
  // 将opts的数据补充完整
  calOptions.call(this)

  // 计算数据
  calSeriesMap.call(this)
  calSeriesColor.call(this)
  calLegendData.call(this)

  // 有相同xy轴的图表，只计算一次
  if (this.seriesMap.line || this.seriesMap.bar || this.seriesMap.scatter || this.seriesMap.candlestick || this.seriesMap.heatmap) {
    calAxisData.call(this)
  }
  Object.keys(this.seriesMap).forEach(type => {
    if (this.seriesMap[type]) {
      switch (type) {
        case 'bar':
          calChartBarData.call(this)
          break
        case 'line':
          calChartLineData.call(this)
          break
        case 'pie':
          calChartPieData.call(this)
          break
        case 'radar':
          calAxisRadarData.call(this)
          calChartRadarData.call(this)
          break
        case 'scatter':
          calChartScatterData.call(this)
          break
        case 'funnel':
          calChartFunnelData.call(this)
          break
        case 'candlestick':
          calChartCandlestickData.call(this)
          break
        case 'heatmap':
          calChartHeatmapData.call(this)
          break
        case 'treemap':
          calChartTreemapData.call(this)
          break
        case 'tagCloud':
          calChartTagCloudData.call(this)
          break
      }
    }
  })

  console.log('complete calChartsData')
}
